import React from 'react'
import {connect} from 'react-redux'
import {
    withRouter
} from 'react-router-dom'
import { NavBar, Icon, TabBar, Button } from 'antd-mobile'
const Child = (props) => {
    console.log(props);
    let id = props.location.id;
    return (
        <div className='doc'>
            <Icon type='left' size='lg' onClick={() => props.history.goBack()} />
            <div>
                <img style={{height:'188px'}} src={props.datalist[id-100].img}/>
            </div>

            <div className="bottomNav" style={{ position: 'fixed', bottom: 0, width: '100%', height: '80px', lineHeight: '80px' }}>
                <div className='btns'>
                    <button style={{ width: '18%', backgroundColor: 'white', border: 'none' }} >
                        <i className='iconfont icon-dianpu'></i>
                        店铺
                    </button>
                    <button style={{ width: '18%', backgroundColor: 'white', border: 'none' }}>
                        <i className='iconfont icon-kefu' ></i>
                        客服
                    </button>
                    <button
                        onClick={
                            (e) => {
                                if(!props.username){
                                    props.history.push('/login')
                                } 
                                console.log(props)
                                props.dispatch({
                                    type: 'COLLECT',
                                    collect: props.datalist[id-100].img
                                })

                            }
                        }
                        style={{ width: '18%', backgroundColor: 'white', border: 'none',fontSize:'12px' }}>
                             {props.arr.some((item) => {
                                return item == props.datalist[id-100].img;
                            })
                                ? <i className='iconfont icon-shoucang1'></i>
                                : <i className='iconfont icon-shoucang'></i>}
                        <span>{props.arr.some((item) => {
                            console.log(item)
                                return item == props.datalist[id-100].img ;
                            }) ? '已收藏' : '收藏'}</span>
                           
                    </button>
                    <button style={{ width: '23%', backgroundColor: ' rgb(255, 133, 42)', border: 'none' }}>
                        加入购物车
                    </button>
                    <button style={{ width: '23%', backgroundColor: ' rgb(255, 133, 42)', border: 'none' }}>
                        立即购买
                    </button>
                </div>
            </div>

        </div>
    )

}


const mapStateToProps = (state) => ({
    arr: state.collect,
    datalist: state.shop.arr,
    username: state.log.username
})
export default connect(mapStateToProps)(withRouter(Child))
