import React, { useEffect } from 'react'
import { WingBlank, Carousel } from 'antd-mobile'
import './tb.css'
import Mytab from './Mytab';
import { connect } from 'react-redux'
import { Link, Router, withRouter } from 'react-router-dom'

// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>

const getList = () => {
  return (dispatch) => {
    let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
    fetch(url, {
      method: 'post',
      headers: {
        'Content-Type': 'application/json'
      },
    })
      .then(res => res.json())
      .then(res => {
        console.log(res.data)
        dispatch({
          type: 'GETLIST',
          arr: res.data
        })
      });
  }
}

const Home = (props) => {
  console.log(props);
  useEffect(() => { props.dispatch(getList()) }
    , [])
  const data = ['https://aecpm.alicdn.com/simba/img/TB12NW9kH1YBuNjSszhSuwUsFXa.jpg',
   'https://img.alicdn.com/imgextra/i1/6000000000720/O1CN01QON2dX1HBnnbupK0g_!!6000000000720-0-octopus.jpg', 
   'https://img.alicdn.com/imgextra/i4/6000000002436/O1CN01r0sGKc1TrjRkVM8w8_!!6000000002436-0-octopus.jpg'], imgHeight = 176;
  return (
    <div>
      <header className="Search">
        <div className="header-bd">
          <a className="placeholder" onClick={() => {props.history.push('/home/search') }}>
            <span className="text iconfont iconsousuo">寻找宝贝店铺</span>
          </a>
        </div>
      </header>
      <Carousel
          autoplay={true}
          infinite
          style={{marginTop:'37px'}}
        >
          {data.map(val => (
            <a
              key={val}
              href="http://www.alipay.com"
              style={{ display: 'inline-block', width: '100%', height: imgHeight }}
            >
              <img
                src={val}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  // imgHeight='auto';
                }}
              />
            </a>
          ))}
        </Carousel>
      <ul className='goods'>

        {
          props.datalist.map((data, i) => (
            <li key={i}
              onClick={() => props.history.push({
                pathname: '/home/child',
                id: data.id
              })}
              className='details'
            >
              <img src={data.img} />
              <p className='p1'>{data.title}</p>
              <p className='p2'>价格：{data.price}</p>
              <p className='p3'>{data.id}人购买</p>

            </li>
          ))
        }
      </ul>

      <Mytab />


    </div>
  )
}

const mapStateToProps = (state) => ({
  datalist: state.shop.arr
})
export default connect(mapStateToProps)(withRouter(Home))