let collect = [];
const collectreducer = (state = collect, action) => {
    if (action.type == 'COLLECT') {
        return Array.from(new Set([...state, action.collect]))
    }
    if (action.type == 'CANCEL') {
        let arr = [...state];
        console.log(arr);
        arr.splice(action.index, 1);
        return arr;
    }
    console.log(action);
    return state;
}
export default collectreducer;