import React from 'react'
import Mytab from './Mytab'
import {connect} from 'react-redux'
import {
    withRouter
} from 'react-router-dom'
const Cart = (props) => {
    return (
        <div>
            <div>
                {props.arr.map((img,i)=>{
                   return <div>
                       <img style={{height:'188px'}}src={img}/>
                       <button id='cancelbutton' onClick={() => props.dispatch({
                                        type: 'CANCEL',
                                        index:i
                                    })}>取消收藏</button>
                   </div>
                })}
                {/* <img src={props.location.src} /> */}
               
            </div>
            <Mytab/>
        </div>
    )
}
const mapStateToProps = (state) => ({
    arr: state.collect
})
export default connect(mapStateToProps)(withRouter(Cart))
