import React,{useState} from 'react'
import {connect} from 'react-redux'
// import {log} from './ActionCreators'
const log=(username)=>{
    return (dispatch)=>{
        let url ='https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login';
        fetch(url,{
            method:'post',
            headers:{
                'Content-Type':'application/json'
            },
        })
        .then(res=>res.json())
        .then(res=>
            {
                console.log(res);
                dispatch(
                    {
                    type:'LOGIN',
                    username:res.data.name 
                    }
                )  
            }
            );
    }
}
const Login = (props) => {
    console.log(props);
    return (
        <div>
            <div style = {{fontsize:'20px',fontWeight:'bold'}}>请您先登录</div>
            <div style = {{width:'200px',height:'300px',margin:'0 auto'}}>
            <from>
                <input type='text' name='username' placeholder='请输入用户名' style={{marginTop:'30px',marginBottom:'30px',width:'200px', height:'20px'}}></input><br></br>
                <input type='password' name='username' placeholder='请输入密码' style={{marginBottom:'30px',width:'200px', height:'20px'}}></input>
            </from>
            <button style={{width:'80px' ,height:'30px',marginLeft:'60px'}}
                onClick={()=>{
                    props.history.push('/cart')
                    props.dispatch(log())
                    }
                }
            >登录</button> 
            </div>
        </div>
    )
}
const mapStateToProps = (state)=>({
    username:state.username
})
export default connect(mapStateToProps)(Login)
